.container-bio {
  background-color: #CCD6B2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-direction: column;
      flex-direction: column;
  min-height: 30rem;
  padding: 2rem;
}
@media all and (max-width: 640px) and (min-width: 1px) {
  .container-bio {
    padding: 3vw;
    min-height: 100vh;
  }
}
.bio-item1 {
  -ms-flex: 1 1;
      flex: 1 1;
}
.bio-item1 h1 {
  font-family: Tangerine;
  font-size: 5rem;
  margin: 0;
  padding: 1rem;
}
@media all and (max-width: 640px) and (min-width: 1px) {
  .bio-item1 h1 {
    font-size: 20vw;
  }
}
.bio-item2 {
  -ms-flex: 1 1;
      flex: 1 1;
}
.bio-item2 .avatar {
  width: 20rem;
  margin: 3rem;
  border-radius: 50%;
  float: left;
}
@media all and (max-width: 640px) and (min-width: 1px) {
  .bio-item2 .avatar {
    width: 50vw;
    margin: 1rem;
    float: initial;
  }
}
.bio-item2 p {
  font-size: 1.5rem;
  text-align: justify;
  margin: 0;
  padding: 1rem;
}
@media all and (max-width: 640px) and (min-width: 1px) {
  .bio-item2 p {
    font-size: 6vw;
  }
}



