.container-header {
  background-image: url("../img/web.jpg");
  background-size: cover;
  opacity: 0.9;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  min-height: 50rem;
}
@media all and (max-width: 640px) and (min-width: 1px) {
  .container-header {
    min-height: 100vh;
  }
}
.header-text {
  background: rgba(0, 0, 0, 0.7);
  width: 60%;
  height: 20rem;
  padding: 1rem;
  color: white;
  border-radius: 1rem;
}
@media all and (max-width: 640px) and (min-width: 1px) {
  .header-text {
    width: 80%;
  }
}
.title {
  font-size: 6rem;
  font-family: Lateef;
  text-shadow: 2px 2px grey;
  height: 6rem;
  margin: 0.5rem;
  padding: 0;
  text-align: center;
}
@media all and (max-width: 640px) and (min-width: 1px) {
  .title {
    font-size: 9vh;
    margin: 0;
  }
}
.subtitle {
  font-size: 1.2rem;
  text-align: center;
  line-height: 2rem;
  position: relative;
  bottom: -1rem;
}
@media all and (max-width: 640px) and (min-width: 1px) {
  .subtitle {
    font-size: 2.5vh;
    margin: 0;
    bottom: 0;
  }
}
.button {
  background-color: rgba(255, 255, 255, 0.2);
  border-width: 0rem;
  border-color: #656B56;
  border-radius: 0.5rem;
  color: white;
  padding: 1rem 2rem;
  text-align: center;
  text-decoration: none;
  font-weight: lighter;
  display: inline-block;
  font-size: 1.5rem;
  position: relative;
  bottom: -1.5rem;
}
@media all and (max-width: 640px) and (min-width: 1px) {
  .button {
    font-size: 3vh;
    bottom: -6vh;
  }
}
.button:hover {
  cursor: -webkit-grab;
  cursor: grab;
  background-color: rgba(255, 255, 255, 0.8);
  color: #A55F45;
  -webkit-transition: all 0.8s;
  -o-transition: all 0.8s;
  transition: all 0.8s;
}



